@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', monospace;
}

.about, .projects, .experience {
  display: flex;
  height: 90vh;
  align-items:center;
  justify-content: center;
  font-size: 3rem;
}

.introduction {
  font-size: 2rem;
  text-align: center;
  padding-left: 100px;
} 

.learnmore {
  top: 2px;
  padding-left: 40px;
  padding-top: 10px;
}

.aboutme {
  font-size: 1.5rem;
  text-align: center;
  padding-right: 80px;
} 

.imageofme {
  padding-left: 80px;
  padding-right: 30px;
}

.projectIntro {
  font-size: 40px;
  text-align: center;
  color: #f2a1a1;
  padding: 0;
  font-weight: bold;
  margin-bottom: -10px;
  padding-top: 20px;
  
}

.projectButton {
  cursor: pointer;
  padding-top: 0;
}

.descriptionProject {
  padding: 30px;
  background-color: #f2a1a1;
  border-radius: 10px;
  border-width: 10px;
  margin-bottom: 15px;
  font-size: 10px;
  width:470px;
}

.experienceIntro {
  font-size: 2rem;
  color: #f2a1a1;
  font-weight: bold;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 25px;
}

.experienceEntry {
  height: 250px;
  width: 950px;
  background-color: #f2a1a1;
  border-radius: 10px;
  border-width: 10px;
  margin-bottom: 20px;
  padding: 10px;
  text-align: center;
  font-size: 0.9rem;
}

@media screen and (max-width: 700px) { 
  .introduction {
    font-size: 1rem;
    padding-top: 50px;
  } 

  .animationofme img {
    width: 260px;
    height: 260px;
    padding-right: 50px;
    padding-top: 50px;
  }
  
  .learnmore {
    padding-left: 20px;
  }

  .learnmore img {
    height: 70px;
    width: 70px;
  }
  
  .aboutme {
    font-size: 0.8rem;
    text-align: center;
    padding-right: 20px;
  } 
  
  .imageofme {
    padding-left: 10px;
    padding-right: 10px;
  }

  .imageofme img {
    height: 150px;
    width: 150px;
  }

  .bottomofabout img {
    height: 100px;
    width: 100px;
  }
  
  .connectingInfo {
    font-size: 13px;
  }

  .projectIntro {
    font-size: 1.3rem;
    padding-bottom: 10px;
    padding-top: 20px;
  }
  
  .projectButtons img {
    height: 100px;
    width: 100px;
  }

  .projects {
    padding-left: 15px;
  }

  .descriptionProject {
    padding: 0.3rem;
    font-size: 7px;
    width:190px;
  }

  .projectDescription {
    padding-right: 0px;
  }

  .experienceIntro {
    font-size: 1.3rem;
  }
  
  .experienceEntry {
    height: 300px;
    width: 300px;
    font-size: 0.6rem;
  }

}

@media screen and (max-width: 980px) and (min-width: 699px) { 
  .introduction {
    font-size: 1.5rem;
    padding-top: 100px;
  } 

  .animationofme img {
    width: 380px;
    height: 410px;
    padding-right: 50px;
    padding-top: 100px;
  }
  
  .learnmore {
    padding-left: 40px;
  }

  .learnmore img {
    height: 85px;
    width: 85px;
  }
  
  .aboutme {
    font-size: 1.2rem;
    text-align: center;
    padding-right: 20px;
  } 
  
  .imageofme {
    padding-left: 20px;
    padding-right: 10px;
  }

  .imageofme img {
    height: 250px;
    width: 250px;
  }

  .bottomofabout img {
    height: 150px;
    width: 150px;
  }
  
  .connectingInfo {
    font-size: 15px;
  }

  .projectIntro {
    font-size: 2rem;
    padding-bottom: 0px;
    padding-top: 20px;
  }
  
  .projectButtons img {
    height: 160px;
    width: 160px;
  }

  .descriptionProject {
    padding: 1.2rem;
    font-size: 10px;
    width:430px;
  }

  .projectDescription {
    padding-right: 0px;
  }

  .experienceIntro {
    font-size: 1.7rem;
  }
  
  .experienceEntry {
    height: 290px;
    width: 500px;
    font-size: 0.8rem;
  }

}





