.navbar {
    background-color: #f2a1a1;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items:center;
    
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #f2a1a1;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f6e0e0;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #f58686;
    cursor: pointer;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #f2a1a1;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

span {
    margin-left: 16px;
}

@media screen and (max-width: 700px) { 
    .navbar {
        height: 50px; 
    }

    .menu-bars {
        margin-left: 1rem;
        font-size: 1.8rem;
    }

    .nav-menu {
        width: 180px;
        height: 40vh;
        justify-content: center;
        align-items: center;
    }

    .nav-text {
        padding: 0px 0px 8px 10px;
        list-style: none;
        height: 40px;
    }

    .nav-text a {
        font-size: 15px;
    }

    .navbar-toggle {
        height: 50px;
    }
}